"use server";

import fs from 'fs';
import path from 'path';
import { v4 as uuidv4 } from 'uuid';

const USERS_DB_PATH = path.join(process.cwd(), 'data', 'users.json');

// Types
export interface User {
    id: string;
    name: string;
    email: string;
    phone: string;
    createdAt: string;
    password?: string;
    // Simulate JOCOLLA SNS fields
    isJocollaUser: boolean;
}

// Helpers
const getUsers = (): User[] => {
    if (!fs.existsSync(USERS_DB_PATH)) {
        fs.writeFileSync(USERS_DB_PATH, JSON.stringify([]));
    }
    try {
        const data = fs.readFileSync(USERS_DB_PATH, 'utf-8');
        return JSON.parse(data);
    } catch {
        return [];
    }
}

const saveUser = (user: User) => {
    const users = getUsers();
    users.push(user);
    fs.writeFileSync(USERS_DB_PATH, JSON.stringify(users, null, 2));
}

export async function registerUser(formData: FormData) {
    const name = formData.get('name') as string;
    const email = formData.get('email') as string;
    const phone = formData.get('phone') as string;
    const password = formData.get('password') as string;

    // Basic validation
    if (!name || !email || !phone || !password) {
        return { success: false, message: '全ての項目を入力してください。' };
    }

    // Check duplicate email
    const users = getUsers();
    if (users.find(u => u.email === email)) {
        return { success: false, message: 'このメールアドレスは既に登録されています。' };
    }

    const newUser: User = {
        id: uuidv4(),
        name,
        email,
        phone,
        password,
        createdAt: new Date().toISOString(),
        isJocollaUser: true
    };

    try {
        saveUser(newUser);
        // Here we would ideally trigger an API call to JOCOLLA SNS or write to their DB
        // console.log("Syncing to JOCOLLA SNS DB...");

        return { success: true, userId: newUser.id };
    } catch (e) {
        return { success: false, message: '登録に失敗しました。' };
    }
}
